#bg {
  position: fixed;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.8;
}

.banner-placeholder {
  height: 50px;
}

.banner {
  background-repeat: no-repeat;
  background-size: 100% 100%;
  height: 50px;
  position: fixed;
  top: 0;
  width: 100%;
}

.main {
  background: none;
}

.app {
  text-align: center;
}

/* override Button */
.btn:focus {
  outline: none !important;
}

/* override Panel */

.panel {
  margin: 0 5px 5px;
}

.panel:first-child {
  margin-top: 5px;
}

.panel-heading {
  align-items: center;
  display: flex;
  justify-content: space-between;
}

.panel-body {
  padding: 5px;
}

/* override Modal */

.modal {
  display: flex !important;
}

.modal-dialog {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
  overflow: auto;
  margin: auto;
  max-height: 90%;
  max-width: 90%;
}

.modal-content {
  display: flex;
  flex-direction: column;
}

.modal-body {
  overflow-x: hidden;
  overflow-y: scroll;
  padding: 10px;
  text-align: center;
}

.modal-footer {
  text-align: center;
}

/* Tunnel & Tunnel Modal */

.tunnel-banner .panel-heading {
  background-size: cover;
  background-position: center;
}

.tunnel-banner.selectable > .panel-heading:hover,
.tunnel > .panel-heading:hover {
  cursor: pointer;
}

.tunnel-banner > * > .panel-body {
  padding-left: 20%;
}

.tunnel-banner .label,
.tunnel .label {
  background-color: rgba(0,0,0,0.5);
  font-size: 18px;
  overflow: auto;
  padding: .2em .2em .3em;
  text-overflow: ellipsis;
}

.tunnel-banner .label.check,
.tunnel .label.check {
  color: green;
  display: inline-block;
  height: 28px;
  width: 28px;
}

.memo {
  margin-top: 1px;
  border: 1px solid black;
}

/* Nameko */

.n-test .nameko {
  /*background-color: '#808834';*/
  background-color: rgba(128, 136, 52, 0.75);
  border-color: #f9f1d9 !important;
}

.n-test .modal-body {
  background-color: #5a391e;
}

.all-block {
  margin-left: -5px;
  margin-right: -5px;
}

.nameko-wrapper {
  padding: 0;
}

.nameko {
  border-radius: 4px;
  border-color: #ccc;
  border: 1px solid #ccc;
  margin: 2px;
  padding: 2px;
}

.nameko.selected {
  border: 2px solid lightblue;
  padding: 1px;
}

.nameko.clickable:active,
.nameko.clickable:focus,
.nameko.clickable:hover {
  background-color: #e6e6e6;
}

.nameko .top-ctn {
  display: flex;
  height: 96px;
}
@media(min-width:768px) {
  .nameko .top-ctn {
    height: 120px;
  }
}

.nameko .img-ctn {
  align-items: center;
  display: flex;
  flex: 3;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
}

.nameko .img {
  max-height: 96px;
  max-width: 100%;
}
@media(min-width:768px) {
  .nameko .img {
    max-height: 120px;
  }
}

.nameko .name {
  background-color: rgba(0,0,0,0.5);
  border-radius: 3px;
  bottom: 10px;
  color: white;
  padding: 2px 10px;
  position: absolute;
}

.skill-ctn {
  display: flex;
  flex-direction: column;
  width: 18px;
}
@media(min-width:768px) {
  .skill-ctn {
    width: 24px;
  }
}

.skill {
  background-color: transparent;
  background-position: center;
  background-size: cover;
  border: none;
  filter: grayscale(100%);
  height: 18px;
  opacity: 0.5;
  width: 18px;
}
@media(min-width:768px) {
  .skill {
    height: 24px;
    width: 24px;
  }
}

.skill.selected {
  filter: grayscale(0%);
  opacity: 1;
}

.value-ctn {
  background-color: rgba(0,0,0,0.5);
  color: white;
  display: flex;
  justify-content: space-around;
}

.value-ctn span {
  background-size: 11px;
  background-repeat: no-repeat;
  background-position-y: center;
  padding-left: 12px;
}
@media(min-width:768px) {
  .value-ctn {
    line-height: 24px;
  }

  .value-ctn span {
    background-size: 22px;
    padding-left: 24px;
  }
}

.value-area {
  display: flex;
  justify-content: center;
}

.value-area > div {
  display: flex;
  flex-direction: column;
  margin: 0 5px;
}

.value-area .btn {
  background-color: lightgray;
  border-width: 0;
  border-radius: 50%;
  height: 44px;
  padding: 0;
  width: 44px;
}

.nameko .glyphicon {
  margin-right: 3px;
}

/* Nameko Modal */

.nameko-modal .modal-body {
  overflow: hidden;
  padding: 0;
}


.list {
  height: calc(100vh - 200px);
  overflow: hidden auto;
  padding: 10px;
  width: 100%;
}

.editor {
  background-color: rgba(0,0,0,0.8);
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
}

/* Result Panel */

.result-panel h1,
.result-panel h2 {
  background-color: white;
  position: sticky;
  margin: 0;
}

.result-panel h1 {
  overflow: hidden;
  text-overflow: ellipsis;
  top: 50px;
  white-space: nowrap;
  z-index: 1;
}

.result-panel h2 {
  top: 89px;
}

/* Run */

.footer-placeholder {
  height: 69px;
}

.footer {
  bottom: 20px;
  left: 50%;
  position: fixed;
  transform: translate(-50%,0);
  z-index: 2;
}

.footer > * {
  margin: 3px 5px 0 5px;
}

.footer .btn {
  box-shadow: 1px 1px 2px 2px rgba(0,0,0,0.3);
}

#sort .glyphicon {
  margin-left: 6px;
}

#sort .caret {
  display: none;
}

#run .glyphicon {
  margin-right: 6px;
}
